import React, { useState } from "react";
import { currentTime, formatAMPM } from "./Time";

function App() {
  const [currentHour, setCurrentHour] = useState(currentTime.currentHour);

  const decrementHour = () => {
    if (currentHour == 0) {
      return setCurrentHour(currentHour + 24);
    }
    return setCurrentHour(currentHour - 1);
  };

  const incrementHour = () => {
    if (currentHour == 24) {
      return setCurrentHour(currentHour - 24);
    }
    return setCurrentHour(currentHour + 1);
  };

  const setDay = () => {
    if (currentHour >= 0 && currentHour <= 12) {
      return "Good Morning! It's " + currentHour + ":00 AM";
    }
    if (currentHour >= 12 && currentHour <= 18) {
      return "Good Afternoon! It's " + currentHour + ":00 PM";
    }
    if (currentHour >= 16 && currentHour <= 24) {
      return "Good Evening! It's " + currentHour + ":00 PM";
    }
  };

  const changeColor = () => {
    changeBg();
    if (currentHour >= 0 && currentHour <= 12) {
      return "red";
    }
    if (currentHour >= 12 && currentHour <= 18) {
      return "green";
    }
    if (currentHour >= 16 && currentHour <= 24) {
      return "blue";
    }
  };

  const changeBg = () => {
    if (currentHour >= 0 && currentHour <= 12) {
      return "linear-gradient(to right, #fa709a 0%, #fee140 100%);";
    }
    if (currentHour >= 12 && currentHour <= 18) {
      return "linear-gradient(120deg, #d4fc79 0%, #96e6a1 100%);";
    }
    if (currentHour >= 16 && currentHour <= 24) {
      document.getElementById("root").style.backgroundImage =
        "linear-gradient(120deg, #84fab0 0%, #8fd3f4 100%);";
    }
  };

  return (
    <>
      <h1 style={{ color: changeColor() }}>{setDay()}</h1>
      <button type="submit" onClick={decrementHour}>
        Decrement -
      </button>
      <button type="submit" onClick={incrementHour}>
        Increment +
      </button>
    </>
  );
}

export default App;
